<template>
    <ContentField>
        <div class="row">
            <div class="col-sm-4">
                <img src="../../assets/6yy4_chain-A.jpeg" width="200px" height="200px">
            </div>
            <div class="col-sm-8">
                <br>
                <br>
                <h3 style="text-align:left">Fast and Accurate G4 formation tendency Prediction with Deep Learning</h3>
            </div>
        </div>

        <div class="shadow-none  bg-light rounded">
            <p class="red">This is the G4 formation tendency predictionwebserver--CPU version</p>
            <h4 class="bold">Usage:</h4>
            <p>1. Please upload your DNA/RNA sequence file or type in fasta sequence in the input box, you can click the
                'Show example data' button below to see an example;</p>
            <p>2. Enter your output file name (no suffix needed) and click the 'Submit' button;</p>
        </div>

        <form>
            <label class="radio-inline">
                <input type="radio" name="optradio" id="opt_single" onclick="checkedSingle();" checked="">DNA sequence
            </label>
            <label class="radio-inline" style="margin-left: 20px;">
                <input type="radio" name="optradio" id="opt_fasta" onclick="checkedFasta();">RNA sequence
            </label>
        </form>

        <div class="shadow-none  bg-light rounded">
            <div class="input-group mb-3">
                <span class="input-group-text">Sequence</span>
                <textarea class="form-control" v-model="sequence"></textarea>
            </div>

            <div class="input-group mb-3">
                <input name="fasta_file" accept=".fasta,.fa,.txt" class="form-control" type="file" id="fasta_file"
                    @change="check_file_size($event)">
            </div>

            <div>
                <button type="button" class="btn btn-outline-dark" @click="handleClick">submit</button>
            </div>
        </div>

        <br>
        <div class="row">
            <table class="table">
                <thead>
                    <tr>
                        <th scope="col">start</th>
                        <th scope="col">end</th>
                        <th scope="col">mismatch</th>
                        <th scope="col">sequence</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in tableData" :key="index">
                        <td>{{ item.start }}</td>
                        <td>{{ item.end }}</td>
                        <td>{{ item.mismatch }}</td>
                        <td>{{ item.sequence }}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <div id="main" style="width: 100%; height: 400px;"></div>
    </ContentField>
</template>

<script>
import $ from 'jquery'
import ContentField from "../../components/ContentField.vue";
import * as echarts from 'echarts';
// import { ref } from 'vue';

export default{
    data() {
        return {
            sequence: '',
            tableData: [],
        }
    },
    components: {
        ContentField
    },
    methods: {
        handleClick() {
            // console.log(this.sequence);
            $.ajax({
                url: "http://127.0.0.1:5000/api/data?sequence=" + this.sequence,
                type: "get",
                success: resp => {
                    console.log(resp);
                    this.tableData = resp;
                }
            });
        },
        check_file_size(event) {
            const selectedFile = event.target.files[0];
            if (selectedFile) {
                const sizeInBytes = selectedFile.size;
                const maxFileSize = 1 * 1024 * 1024; // 10 MB in bytes

                if (sizeInBytes > maxFileSize) {
                    alert('文件大小不能超过10MB');
                    // 清除已选择的文件
                    event.target.value = null;
                }
            }
        }
    },
    mounted() {
        const chartDom = document.getElementById('main');
        if (chartDom) {
            const myChart = echarts.init(chartDom);

            // 设置图表的option
            const option = {
                title: {
                    text: 'Top 10 attention values'
                },
                tooltip: {},
                xAxis: {
                    data: ['GTGGGGGGGGGGGGG', 'GGTGGGGGGGGGGGG', 'AGCGGTGGGGGGGGG', 'TGGGGGGGGGGGGGT', 'GGGGGGGGGGGGGTG', 'GGGGGGGGGGGGTGG', 'GCGGTGGGGGGGGGG', 'GGGGGGGGGGGTGGG', 'CGGTGGGGGGGGGGG', 'CTAGCGGTGGGGGGG'],
                    axisLabel: { // 增加x轴标签配置
                        rotate: 25, // 设置旋转角度为45度
                        margin: 10, // 可能需要调整间距避免重叠
                        align: 'right' // 根据实际情况选择对齐方式
                    }
                },
                yAxis: {},
                series: [
                    {
                        name: '销量',
                        type: 'bar',
                        data: [6.2942918, 5.90178057, 5.56924276, 5.18956353, 5.06133313, 4.70050685, 4.357842, 4.10454745, 4.07986106, 3.51715075]
                    }
                ]
            };

            // 绘制图表
            myChart.setOption(option);
        } else {
            console.error('未找到ID为"main"的元素来初始化ECharts实例');
        }
    }
}


</script>

<style scoped>
    table {
        width: 100%;
        table-layout: fixed;
        border-collapse: collapse; /* 合并边框，避免双线 */
        }
        th, td {
        border: 1px solid #ddd; /* 表格边框 */
        text-align: left; /* 文本对齐方式 */
        padding: 8px; /* 单元格内边距 */
        }
        tr:nth-child(even){background-color: #f2f2f2;} /* 偶数行背景色 */
        th {
        background-color: #474d47; /* 表头背景色 */
        color: white; /* 表头文字颜色 */
    }
</style>
