import { createRouter, createWebHistory } from 'vue-router'
import PredictView from '../views/predict/PredictView.vue'
import DownloadView from '../views/download/DownloadView.vue'
import ConcatView from '../views/concat/ConcatView.vue'
import NotFound from '../views/error/NotFound.vue'

const routes = [
  {
    path: "/",
    redirect: "/predict/",
    name: "home"
  },
  {
    path: "/predict/",
    component: PredictView,
    name: "predict_idx"
  },
  {
    path: "/download/",
    component: DownloadView,
    name: "download_idx"
  },
  {
    path: "/concat/",
    component: ConcatView,
    name: "concat_idx"
  },
  {
    path: "/404/",
    component: NotFound,
    name: "404"
  },
  {
    path: "/:catchAll(.*)",
    redirect: "/404/",
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
